<style>
    a {
        color: #fff;
    }

    .np-side-menu {
		border-radius: 0.5em 0 0 0;
		background-color: #2F3136;
		color: #fff;
        height: 100%;
    }
    .for-scroll {
        padding: 1em;
        width: calc(100% - 5px);
        overflow-y: hidden;
        height: 100%;
    }
    .for-scroll:hover {
        overflow-y: scroll;
        width: 100%;
    }
    .list-menu-item {
        padding: 0;
    }
    .list-menu-item li {
        display: block;
        padding: 0.8em 1em;
        font-size: 1.1em;
        border-radius: 0.4em;
        margin-bottom: 0.2em;
    }
    .list-menu-item li:hover {
        background-color: #4f535a;
        cursor: pointer;
    }
    .list-menu-item .active {
        background-color: #7289DA!important;
    }
    .icon {
        float: left;
        width: 2.4em;
    }

    .np-side-menu .list-item-header {
        color: #A0A0A0;
        font-weight: 500;
    }

    ::-webkit-scrollbar {
        width: 5px;
        padding: 0;
	}
	::-webkit-scrollbar-thumb {
		min-height:50px;
		background-color:#202225;
		height: 50px;
	}
	::-webkit-scrollbar-track {
		background: #2E3338;
	}
</style>

<script>
    //DEPENDENCY
    import { onMount } from 'svelte';

    //VARIABLE
    let timeState = 'DAY';

    onMount(async () => {
        setupActiveClass();

        let dt = new Date();
        var h =  dt.getHours(), m = dt.getMinutes();
        var _time = ((h < 6) || (h >= 20)) ? ('NIGHT') : ('DAY');
        timeState = _time;

        // console.log(_time);
    });

    function addActiveClass() {
        let list_menu_item = document.getElementById('side-menu-list-item2');
        let listLi = list_menu_item.getElementsByTagName("li");

        for(let i=0; i < listLi.length; i++) {
            let tmpActive = listLi[i];
            tmpActive.className = tmpActive.className.replace(" active","");
        }

        let tmpNewActive = this.getElementsByTagName("li");
        tmpNewActive[0].className += " active";
    }

    function setupActiveClass() {
        let tmpURL = window.location.href.replace(window.location.origin,"");
        tmpURL = tmpURL.split("/");
        
        let list_menu_item = document.getElementById('side-menu-list-item2');
        let listLi = list_menu_item.getElementsByTagName("li");

        for(let i=0; i < listLi.length; i++) {
            let tmpActive = listLi[i];
            if(tmpURL[1] === tmpActive.getAttribute("data-link-name")) {
                tmpActive.className += " active";
            } else {
                tmpActive.className = tmpActive.className.replace(" active", "");
            }
        }
    }
</script>

