<style>
	.chapter-item {
		display: block;
		border-radius: 0.4em;
		margin-bottom: 0.4em;
	}
	.chapter-item .chapter-name {
		font-size: 1.1em;
	}
	a:visited .chapter-name {
		color: #1abc9c!important;
	}

	b {
		color: #1abc9c!important;
	}
	.licenseProject {
		color: #fff;
		text-align: center;
		font-size: 2em;
	}
</style>

<script>
	import { showConvertDate } from '../../components/uiUtility.svelte';
	import { SkeletonText } from 'skeleton-elements/svelte';

	let tmpArray6 = [1,2,3,4,5,6];
	export let chapterList = null;
	export let flgLicense = false;
	export let licenseBy = "";

	$:chunkedChapterList = chunkArray(chapterList, 3);

	function chunkArray(arr,n){
		if(chapterList !== null) {
			var chunkLength = Math.max(arr.length/n ,1);
			var chunks = [];
			for (var i = 0; i < n; i++) {
				if(chunkLength*(i+1)<=arr.length)chunks.push(arr.slice(chunkLength*i, chunkLength*(i+1)));
			}
			return chunks; 
		} else {
			return null;
		}
    	
	}

	function showProperChapterName(chapterItem) {
		if(chapterItem.nc_chapter_name.length == 0) {
			return "Chapter " + chapterItem.nc_chapter_no;
		} else {
			return chapterItem.nc_chapter_name;
		}
	}

	function convertProperURL(urlStr) {
		// let result = "";
		let result = urlStr.replace(/\/$/, '');
		let tmpURL = result.split("?");
		if(tmpURL.length > 1) {
			result = tmpURL[0];
		}
		return result;
	}

</script>

<div class="card card-custom gutter-b bg-darker">
	<div class="p-8">
		<h3 class="text-white"><i class="fad fa-list-ol"></i> Chapter List</h3>
		<br/>
		<div class="row">
			{#if chunkedChapterList != null}
				{#if chunkedChapterList.length > 0}
					{#each chunkedChapterList as groupChapter}
						<div class="col-xl-4">
							{#each groupChapter as itemChapter}
							<a href="{convertProperURL(window.location.href)}/{itemChapter.nc_chapter_no}" target="_parent">
								<div class="bg-inner-content chapter-item p-2">
									<b>Ch.{itemChapter.nc_chapter_no}</b> - <span class="chapter-name">{showProperChapterName(itemChapter)}</span>
									<br/><b>By</b> {itemChapter.nc_provider}
									<div class="float-right mr-2"><b>on</b> {showConvertDate(itemChapter.nc_created_date)}</div>
								</div>
							</a>
							{/each}
						</div>
					{/each}
				{:else if flgLicense !== true}
					<div class="col-xl-4">
						<div class="bg-inner-content chapter-item p-2">
							<b><SkeletonText effect='wave'>....................................</SkeletonText>
							<br/><b>By</b> <SkeletonText effect='wave'>..................</SkeletonText>
							<div class="float-right mr-2"><b>on</b> <SkeletonText effect='wave'>..................</SkeletonText></div>
						</div>
					</div>
				{:else}
				<div class="licenseProject">
					This project has been licensed by {licenseBy}, Please supports their product :)
				</div>
				{/if}
			{:else}
				<p class='pl-4'>No chapter... ;w;</p>
				<!-- <div class="col-xl-4">
					<div class="bg-inner-content chapter-item p-2">
						<b><SkeletonText effect='wave'>....................................</SkeletonText>
						<br/><b>By</b> <SkeletonText effect='wave'>..................</SkeletonText>
						<div class="float-right mr-2"><b>on</b> <SkeletonText effect='wave'>..................</SkeletonText></div>
					</div>
				</div> -->
			{/if}
		</div>
	</div>
	
</div>