<style>
	.box {
		display: block;
		border-radius: 0.4em;
		position: relative;
		overflow: hidden;
		transition: box-shadow .3s;
	}
	.box:hover {
		cursor: pointer;
		transition: 0.3s;
		box-shadow: 0 0 11px rgba(0, 0, 0, 0.3); 
	}
	.proj-window {
		margin-bottom: 1em;
		width: 100%;
		padding-top: 160%;
	}
	.img-preview {
		position: absolute;
		top: 0;
		width: 100%;
		height: 80%;
		background-color: #fff;
		overflow: hidden;
    }
    .img-preview:hover {
        background-color: #202225;
    }
	.img-preview img {
		width: 100%;
		border-radius: 0.4em;
		display: block;
		margin: auto;
		position: absolute;
        transition: transform .2s;
	}
	.proj-window:hover .img-preview img {
		transform: scale(1.02);
		margin: auto;
	}
	.item-info {
		position: absolute;
		bottom: 0;
		width: 100%;
		height: 75px;
		overflow: hidden;
        padding-left: 0.2em;
        background-color: #36393F;
        border-radius: 0 0 0.4em 0.4em;
	}
	.project-title {
		font-weight: 500;
		font-size: 1.1em;
		display: block;
		line-height: 110%;
		padding-top: 0.2em;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	span {
		display: block;
		text-overflow: ellipsis;
		white-space: nowrap;
		overflow: hidden;
        max-height: 1.4em;
        color: #989A9D;
	}
	
	.more {
		text-align: center;
		padding: 1em 0;
		height: 48px;
		color: #fff;
		border-top: 3px solid rgb(82, 82, 82);
	}
	.more:hover {
		background-color: #202225;
		cursor: pointer;
	}
</style>

<script>
	import { SkeletonText, SkeletonImage } from 'skeleton-elements/svelte';
	import { base_url, base_url_fs, base_url_api } from '../configure.js';
	import ImageItem from '../components/ImageItem.svelte';
	import { onMount } from 'svelte';
	import { getDatestampForJson, createTmpArray, showConvertDate, getProjectSpecByCode } from '../components/uiUtility.svelte';
	
	export let projType = ''; //Default as 'm'
	export let listSize = 0;
	export let listType = ''; //l=Latest chapter, r=Random, c=Completed,n=New Arrival
	export let isEnableMore = false;
	let isEndLoad = false;

	let tmpArray = [];
	let tmpArrayAll = [];
	let gridSize = "";
	let projectSpec = { 
		fullNameCap: '', 
		fullName: '', 
		dir: '' 
	};
	let offset = 0;

	let tmpLoadingArray = [];
	let flagLoading;

	onMount(async () => {
		flagLoading = true;
		//Compute GridSize
		gridSize = calculateGridSize(listSize);

		projectSpec = getProjectSpecByCode(projType);
		
		//Mock array size for loading box
		tmpLoadingArray = createTmpArray(listSize);

		//Real loading data of latest update
		tmpArrayAll = await loadLatestUpdateChapter();
		if(tmpArrayAll !== []) {
			tmpArray = tmpArrayAll.splice(0, listSize);
		}
	});

	async function loadLatestUpdateChapter() {
		let res_data = [];
		let tmpResponseData = [];
		const res = await fetch(base_url_api + 'getLatestChapterOffset/' + projType + '/' + offset + getDatestampForJson(5));
        if(res.ok) {
			res_data = await res.json();
			
			if(res_data.code === "RES-20001") {
				flagLoading = false;
				tmpResponseData = res_data.listItem;
			} else {
				//TODO - ERROR HAPPENDED
			}
        } else {
            console.log("ERROR: loadLatestUpdateChapter");
			throw new Error(res.json());
        }

		flagLoading = false;
		return tmpResponseData;
	}

	function calculateGridSize(size) {
		let tmpGridSize = "";

		if(size === '3') {
			tmpGridSize = "col-6 col-sm-6 col-md-6 col-lg-6 col-xl-4";
		} else {
			tmpGridSize = "col-6 col-sm-4 col-md-3 col-lg-3 col-xl-2";
		}
		return tmpGridSize;
	}

	async function handleMore() {
		//Step 1 : If current item remains left than listSize, do load ajax
		if(tmpArrayAll.length < listSize) {
			flagLoading = true;
			offset = offset + 1;
			let tmpOffsetItem = await loadLatestUpdateChapter();
			tmpArrayAll = tmpArrayAll.concat(tmpOffsetItem);
		}

		//Step 2 : Assign 
		let tmpAddArray = tmpArray.concat(tmpArrayAll.splice(0, listSize));
		tmpArray = tmpAddArray;
		
		if(offset === 7) {
			isEndLoad = true;
		}
	}
</script>
<div class="p-2">
	<div class="row">
		{#each tmpArray as item}
			<div class="{gridSize} p-2">
				<a href="./{projectSpec.fullName}/{item.np_project_id}" alt="">
				<div class="box proj-window">
					<div class="img-preview">
						<img src="{base_url_fs}{projectSpec.dir}/{item.np_project_id}/{item.np_project_id}_cover.jpg{getDatestampForJson(60)}" alt="" onerror="this.src='./demo/crissXcross.png';" />
					</div>
					<div class="item-info">
						<div class="p-2">
							<b class="project-title">{item.np_name}</b>
							<span style="color: #989A9D">Ch.{item.nc_chapter_no} - {item.nc_chapter_name}</span>
						</div>
						<div class="pl-2 pr-2">
							<div class="float-left">
								<i class="fad fa-book-open"></i> {item.nc_provider}
							</div>
							<div class="float-right">
								{showConvertDate(item.nc_created_date)}
							</div>
						</div>
					</div>
				</div></a>
			</div>
		
		{/each}

		{#if flagLoading === true}
			{#each tmpLoadingArray as item}
				<div class="{gridSize} p-2">
					<div class="box proj-window">
						<div class="img-preview" style="background-color: #36393F;">
							<SkeletonImage effect='wave' height='1700' />
						</div>
						<div class="item-info">
							<div class="p-2">
								<b class="project-title"><SkeletonText effect='wave'>...........................</SkeletonText></b>
								<span style="color: #989A9D"></span>
							</div>
							<div class="pl-2 pr-2">
								<div class="float-left">
									<SkeletonText effect='wave'>..................</SkeletonText>
								</div>
								<div class="float-right">
									<SkeletonText effect='wave'>..........</SkeletonText>
								</div>
							</div>
						</div>
					</div>
				</div>
			{/each}
		{/if}
	</div>
</div>
{#if isEnableMore === 'true'}
	{#if isEndLoad === true}
		<div class="more">Done load content ;w;b</div>
	{:else}
		<div class="more" on:click={handleMore}><i class="fa fa-chevron-down"></i></div>
	{/if}
{/if}
