<style>
	.h100 {
		height: 100%;
	}
	
	/* MAIN CONTENT & SCROLL */
	@media (max-width: 992px) {
		.super-np-content {
			width: 100%!important;
		}
	}
	.super-np-content {
		width: calc(100% - 250px);
	}
	.np-content {
		border-radius: 0.5em 0.5em 0 0;
		background-color: #36393F;
		color: #fff;
		height: 100%;
		padding: 0.5em 0.25em;
	}
	.for-scroll {
        padding: 0.5em 1.75em;
        width: calc(100% - 5px);
        /* overflow-y: scroll; */
		/* overflow-x: hidden; */
        height: 100%;
	}
	::-webkit-scrollbar {
		width: 5px;
		padding:0px;
	}
	::-webkit-scrollbar-thumb {
		min-height:50px;
		background-color:#202225;
		height: 50px;
	}
	::-webkit-scrollbar-track {
		background: #2e3338;
	}

	.fix-padding-mobile {
		padding: 0em 0.5em!important;
	}

</style>

<script>
	import { pageName } from '../src/stores.js';

	//Routing
	import Router from './routing/Router.svelte';
	import Route from './routing/Route.svelte';
	import NotFound from './routing/NotFound.svelte';

	//Pages
	import PageIndex from './pages/index.svelte';
	import PageBlog from './pages/blog.svelte';
	import PageEditor from './pages/editor.svelte';
	import PageEditorDetail from './pages/editorDetail.svelte';

	import PageExplorer from './pages/explorer.svelte';
	import PageProjectMain from './pages/projectMain.svelte';
	import PageProjectDetail from './pages/projectDetail.svelte';
	import PageReaderComic from './pages/readerComic.svelte';
	import PageReaderNovel from './pages/readerNovel.svelte';

	import PageHowTo from './pages/howto.svelte';

	import PageOthers from './pages/others.svelte';
	import PageSocialNetwork from './pages/socialNetwork.svelte';

	import PageSupport from './pages/support.svelte';
	import Page404 from './pages/404.svelte';
	
	//ComponentX
	import SideMenu from './comp/sideMenu.svelte';
	import Header from './comp/header.svelte';
	import Footer from './comp/main/footer.svelte';

	let isReader = false;

	function handleRenderReader(event){
		isReader = true;
	}

	//Title
	$:title = $pageName ? $pageName + ' | Nekopost.net' : '...';
	$: {
		document.title = title;
	}

	let windowHeight;
	let windowWidth;

</script>

<svelte:head>
	<title>{title}</title>
</svelte:head>

<svelte:window 
    bind:innerHeight={windowHeight} 
    bind:innerWidth={windowWidth}
/>

<Router>

	{#if isReader === false }
	<Header/> <!-- HEADER -->
	<div class="container-fluid" style="height: calc(100% - 40px);">
		<div class="row pt-4 pb-0 mb-0 h100">
			{#if windowWidth > 991}
			<div class="d-none d-lg-block m-0 p-0 h100" style="float: left; width: 250px;">
				<SideMenu/>
			</div>
			{/if}
			<div class="m-0 p-0 h100 super-np-content">
				<div class="np-content">
					<div class="for-scroll{windowWidth <= 991 ? " fix-padding-mobile":"" }">
						<!-- Main Menu -->
						<Route path="/" component={PageIndex} />
						<Route path="/blog" component={PageBlog} />
						<Route path="/editor" component={PageEditor} />
						<Route path="/editor/:eid" component={PageEditorDetail} sParam={{'editorId':123}}/>

						<!-- Project -->
						<Route path="/explore" component={PageExplorer} />
						<Route path="/manga" component={PageProjectMain} sParam={{'projectType':'m', 'routeType':'normal'}}></Route>
						<Route path="/manga/:pid" component={PageProjectDetail} sParam={{'projectType':'m'}}></Route>
						<Route path="/project" component={PageProjectMain} sParam={{'projectType':'m', 'routeType':'bypass'}}></Route>
						<Route path="/project/:pid" component={PageProjectDetail} sParam={{'projectType':'m'}}></Route>
						
						<Route path="/comic" component={PageProjectMain} sParam={{'projectType':'d'}}></Route>
						<Route path="/comic/:pid" component={PageProjectDetail} sParam={{'projectType':'d'}}></Route>
						<Route path="/doujin" component={PageProjectMain} sParam={{'projectType':'d'}}></Route>
						<Route path="/doujin/:pid" component={PageProjectDetail} sParam={{'projectType':'d'}}></Route>
						
						<Route path="/novel" component={PageProjectMain} sParam={{'projectType':'n'}}></Route>
						<Route path="/novel/:pid" component={PageProjectDetail} sParam={{'projectType':'n'}}></Route>
						
						<Route path="/fiction" component={PageProjectMain} sParam={{'projectType':'f'}}></Route>
						<Route path="/fiction/:pid" component={PageProjectDetail} sParam={{'projectType':'f'}}></Route>

						<!-- SOCIAL & SUPPORT -->
						<Route path="/social-network" component={PageSocialNetwork}></Route>
						<!-- <Route path="/social-fb" component={PageSocialNetwork} sParam={{'socialType':'fb'}}></Route>
						<Route path="/social-line" component={PageSocialNetwork} sParam={{'socialType':'line'}}></Route>
						<Route path="/social-discord" component={PageSocialNetwork} sParam={{'socialType':'discord'}}></Route> -->
						<Route path="/howto" component={PageHowTo}></Route>

						<!-- OTHER -->
						<Route path="/about" component={PageOthers} sParam={{'pageType':'about'}}></Route>
						<Route path="/privacy" component={PageOthers} sParam={{'pageType':'privacy'}}></Route>
						<Route path="/term" component={PageOthers} sParam={{'pageType':'term'}}></Route>

						<Route path="/support" component={PageSupport} sParam={{'pageType':''}}></Route>

						<Route path="/404" component={Page404}></Route>

						<Footer/>
					</div>
				</div>
			</div>
		</div>	
	</div>
	{/if}

	<!-- READER -->
	<Route path="/comic/:pid/:cno" component={PageReaderComic} sParam={{'projectType':'d'}} on:returnPath={handleRenderReader}></Route>
	<Route path="/manga/:pid/:cno" component={PageReaderComic} sParam={{'projectType':'m'}} on:returnPath={handleRenderReader}></Route>
	<Route path="/novel/:pid/:cno" component={PageReaderNovel} sParam={{'projectType':'n'}} on:returnPath={handleRenderReader}></Route>
	<NotFound>Not Found!!!</NotFound>
</Router>