<script>
    export let item = '';
    export let lazy = false;
    export let styleX = '';
    export let classX = '';
    export let pageSide = '';

    let src = lazy ? "" : item;
    let observer = null;

    $:if(lazy == false) {
        src = item;
    } else {
        observer = new IntersectionObserver(onIntersect, {rootMargin: '200px'});
    }

    function onIntersect(entries) {
        if (!src && entries[0].isIntersecting) {
            src = item;
            //console.log("Transfer!");
        }
    }

    function lazyLoad(node) {
        //console.log("Lazyload triggered!");
        observer && observer.observe(node);

        return {
            destroy() {
                observer && observer.unobserve(node)
            }
        }
    }
</script>

<style>
    article {
        min-width: 300px;
        min-height: 300px;
        margin-bottom: 0.5rem;
    }

    img {
        box-shadow: 0.1em;
        height: 100%;
        max-width: 1200px;
        border: 0.2em solid #fff;
    }

    @media (max-width: 992px) { 
        img {
            width: 100%!important;
        }
    }
</style>

<!-- {observer}
<br/>LAZY = {lazy}
<br/>SRC = {src}
<br/>ITEM = {item} -->
<article use:lazyLoad style="{pageSide == 'left' ? "border-left: 0.2em solid #000;":"border-right: 0.2em solid #000;"}">
  <img src={src}
    class='{classX}' 
    style='{styleX}' 
    alt='' />
</article>