    }
    .for-scroll {
        padding: 1em;
        width: calc(100% - 5px);
        overflow-y: auto;
        height: 100%;
    }
    .list-menu-item {
        padding: 0;
    }
    .list-menu-item li {
        display: block;
        padding: 0.8em 1em;
        font-size: 1.1em;
        border-radius: 0.4em;
        margin-bottom: 0.2em;
    }
    .list-menu-item li:hover {
        background-color: #4f535a;
        cursor: pointer;
    }
    .list-menu-item .active {
        background-color: #7289DA!important;
    }
    .icon {
        float: left;
        width: 2.8em;
    }
    .icon i {
        font-size: 1.1em;
    }

    .np-side-menu .list-item-header {
        color: #A0A0A0;
        font-weight: 500;
    }

    hr {
        border-color: #A0A0A0;
    }

    ::-webkit-scrollbar {
        width: 8px;
        padding: 0;
	}
	::-webkit-scrollbar-thumb {
		min-height:50px;
		background-color:#606060;
		height: 50px;
        border-radius: 0.3em;
	}
	::-webkit-scrollbar-track {
		background: #202225;
	}
</style>

