<style>
	.np-header {
		height: 40px;
	}
	h2 {
		color: #fff;
	}
	.focus {
		color: #02b875;
	}
	.logo {
		height: 30px;
	}
</style>

<script>
	import FloatingSidebar from './header/FloatingSidebar.svelte';
	// import Search from "../components/Search.svelte";

	function addActiveClass() {
        let list_menu_item = document.getElementById('side-menu-list-item');
        let listLi = list_menu_item.getElementsByTagName("li");

        for(let i=0; i < listLi.length; i++) {
            let tmpActive = listLi[i];
			tmpActive.className = tmpActive.className.replace(" active","");

			if(tmpActive.getAttribute('data-link-name') === "") {
				tmpActive.className += " active";
			}
        }
    }

</script>

<div class="d-block d-xs-block d-sm-block d-md-block d-lg-none d-xl-none">
	<FloatingSidebar/>
</div>

<div class="np-header">
	<div class="float-left mt-3 pl-4 mr-2 pt-0">
		<div class="d-block d-xs-block d-sm-block d-md-block d-lg-none d-xl-none">
			<button class="btn mt-2 pt-2 burger-icon ml-4" id="kt_header_mobile_toggle"><span></span></button>
		</div>
	</div>
	<a href="/" on:click={addActiveClass} alt="Nekopost homepage">
		<div class="float-left mt-4 pt-1 pl-4" style="width: 245px;">
			<h2><img src="demo/nekopost_logo.png" class="logo" alt="" /> NEKOPOST<span class="focus">.NET</span></h2>
		</div>
	</a>
	<div>
		<!-- <Search/> -->
	</div>
	<div class="float-right mt-4 pr-4 text-white mr-2 pt-1 d-none d-lg-block">
		<i class="fad fa-dungeon"></i> <a href="https://backend.nekopost.net" alt="backend for editor" class="text-white">Backend</a>
	</div>
</div>