<script context="module">

    //URL
    export function convertProperURL(urlStr) {
		// let result = "";
		let result = urlStr.replace(/\/$/, '');
		let tmpURL = result.split("?");
		if(tmpURL.length > 1) {
			result = tmpURL[0];
		}
		return result;
	}
    export function convertReaderURLComment(urlStr) {
        // let result = "";
		let result = urlStr.replace(/\/$/, '');
		let tmpURL = result.split("#");
		if(tmpURL.length > 1) {
			result = tmpURL[0];
		}
		return result;
    }
    export function scrollToTopByDiv(objectId) {
        let elemnt = document.getElementById(objectId);
        elemnt.scrollIntoView();
    }
    export function validURL(str) {
        var pattern = new RegExp('^(https?:\\/\\/)?'+ // protocol
        '((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.)+[a-z]{2,}|'+ // domain name
        '((\\d{1,3}\\.){3}\\d{1,3}))'+ // OR ip (v4) address
        '(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*'+ // port and path
        '(\\?[;&a-z\\d%_.~+=-]*)?'+ // query string
        '(\\#[-a-z\\d_]*)?$','i'); // fragment locator
        return !!pattern.test(str);
    }

    export function createTmpArray(size) {
        let tmpListItem = [];
        for(let i=0; i < size; i++) {
            tmpListItem.push({'id':i});
        }
        return tmpListItem;
    }

    function pad2(n) { return n < 10 ? '0' + n : n }
    export function getDatestampForJson(inputMinute) {
        let result = '';
        let date = new Date();

        let tmpMin = pad2(date.getMinutes());
        let useMin = '' + (tmpMin/inputMinute - (tmpMin%inputMinute)/inputMinute)*inputMinute;
        
        // result = useMin;
        result = '?' + date.getFullYear().toString()
                + pad2(date.getMonth() + 1)
                + pad2(date.getDate()) 
                + pad2(date.getHours())
                + useMin;

        return  result;
    }

    export function showConvertDate(inputDate) {
        // let tmpDate = new Date(inputDate);
        inputDate = inputDate.replace(" ", "T") + "+0700";
        let tmpDate = new Date(Date.fromISO(inputDate));
        tmpDate.addHours(-7);
        const todayCheck = new Date();

        if(todayCheck.getDate() == tmpDate.getDate()
            && todayCheck.getMonth() == tmpDate.getMonth()
            && todayCheck.getFullYear() == tmpDate.getFullYear()) {
                return "Today";
        } else {
            let result = Math.floor((todayCheck.getTime() - tmpDate.getTime())/(1000*60*60*24));
            if(result === 1) {
                result = "1 day ago";
            } else if(result === 0) {
                result = Math.floor((todayCheck.getTime() - tmpDate.getTime())/(1000*60*60)) + " hours ago";
            } else {

                if(result > 365) {
                    result = Math.floor(result/365);
                    if(result > 2) {
                        result = result + " years ago";
                    } else {
                        result = result + " year ago";
                    }
                } else {
                    result = numberWithCommas(result) + " days ago";
                }
                
            }

            return result;
        }
    }
    Date.prototype.addHours = function(h) {
        this.setTime(this.getTime() + (h*60*60*1000));
        return this;
    }
    Date.fromISO= (function(){
        var testIso = '2011-11-24T09:00:27+0700';
        // Chrome
        var diso= Date.parse(testIso);
        if(diso===1322118027000) return function(s){
            return new Date(Date.parse(s));
        }
        // JS 1.8 gecko
        var noOffset = function(s) {
        var day= s.slice(0,-5).split(/\D/).map(function(itm){
            return parseInt(itm, 10) || 0;
        });
        day[1]-= 1;
        day= new Date(Date.UTC.apply(Date, day));  
        var offsetString = s.slice(-5)
        var offset = parseInt(offsetString,10)/100; 
        if (offsetString.slice(0,1)=="+") offset*=-1;
        day.setHours(day.getHours()+offset);
        return day.getTime();
        }
        if (noOffset(testIso)===1322118027000) {
        return noOffset;
        }  
        return function(s){ // kennebec@SO + QTax@SO
            var day, tz, 
    //        rx = /^(\d{4}\-\d\d\-\d\d([tT][\d:\.]*)?)([zZ]|([+\-])(\d{4}))?$/,
            rx = /^(\d{4}\-\d\d\-\d\d([tT][\d:\.]*)?)([zZ]|([+\-])(\d\d):?(\d\d))?$/,
                
            p= rx.exec(s) || [];
            if(p[1]){
                day= p[1].split(/\D/).map(function(itm){
                    return parseInt(itm, 10) || 0;
                });
                day[1]-= 1;
                day= new Date(Date.UTC.apply(Date, day));
                if(!day.getDate()) return NaN;
                if(p[5]){
                    tz= parseInt(p[5], 10)/100*60;
                    if(p[6]) tz += parseInt(p[6], 10);
                    if(p[4]== "+") tz*= -1;
                    if(tz) day.setUTCMinutes(day.getUTCMinutes()+ tz);
                }
                return day;
            }
            return NaN;
        }
    })()

    export function showDatePattern(inputDate) {
        let tmpDate = new Date(inputDate);
        const todayCheck = new Date();

        if(todayCheck.getDate() == tmpDate.getDate()
            && todayCheck.getMonth() == tmpDate.getMonth()
            && todayCheck.getFullYear() == tmpDate.getFullYear()) {
                return "Today";
        } else {
            return tmpDate.getDate() + "-" + tmpDate.getMonth() + "-" + tmpDate.getFullYear();
        }
    }

    export function numberWithCommas(x) {
        return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    }

    export function getTitleFromCode(code) {
        let result = "";
        switch(code) {
            case "last_chapter":
                result = "Latest Chapter";
                break;
            case "random":
                result = "Random";
                break;
            case "new_arrival":
                result = "New Arrival";
                break;
        }

        return result;
    }

    export function convertProjectTypeFromCode(inputStr) {
        let result = "...";
        switch(inputStr) {
            case "m" : result = "Manga";
            break;
            case "n" : result = "Novel";
            break;
            case "f" : result = "Original Novel";
            break;
            case "d" : result = "Original Comic";
            break;
        }
        return result;
    }

    export function convertProjectStatusFromDBCode(inputStr) {
        let result = "Loading...";
        switch(inputStr) {
            case "0": 
                result = "Inactive";
            break;

            case "1": 
                result = "Active";
            break;

            case "2": 
                result = "Completed";
            break;

            case "3": 
                result = "Licensed";
            break;

            case "4": 
                result = "Banned";
            break;
        }

        return result;
    }

    export function getProjectSpecByCode(shortStr) {
        //input : m
        /*
            output : { 
                fullNameCap: 'Manga', 
                fullName: 'manga', 
                dir: 'collectManga' 
            }
        */
       let result = {
           fullNameCap: null,
           fullName: null,
           dir: null
       }
       switch(shortStr) {
           case "m":
               result.fullName = "manga";
               result.fullNameCap = "Manga";
               result.dir = "collectManga";
               break;
            case "d":
               result.fullName = "comic";
               result.fullNameCap = "Original Comic";
               result.dir = "collectManga";
               break;
            case "n":
               result.fullName = "novel";
               result.fullNameCap = "Novel";
               result.dir = "collectManga";
               break;
            case "f":
               result.fullName = "novel";
               result.fullNameCap = "Original Novel";
               result.dir = "collectManga";
               break;
       }

       return result;
    }

    //TEXT MISC
    export function toUpper(stringInput) {
        return (stringInput + "").toUpperCase();
    }

    //ARRAY MISC
    export function chunkArray(inputArray, chunk_size) {
        let res = [];

        let countPerGroup = Math.ceil(inputArray.length / chunk_size);

        for(let i=0; i < chunk_size; i++) {
            let tmpArray = inputArray.slice(i*countPerGroup, (i+1)*countPerGroup);
            res.push(tmpArray);
        }

        return res;
    }

</script>