<style>
    @media print {
        .print-content { 
            visibility: hidden!important; 
            display: none!important;
            size: auto; 
            margin: 0mm;
        }
        @page { margin: 0; }
        .ptow {
            visibility: visible!important;
            display: block!important;
            left: 25%;
            top: 30%;
            position: absolute;
            z-index:auto;
        }
        .ptow img {
            width: 100%!important;
            min-width: 600px;
        }
    }
    .ptow {
        visibility: hidden; 
        display: none;
    }
    .btnTop {
        display: block;
        position: fixed;
        bottom: 20px;
        right: 30px;
        z-index: 99;
        font-size: 18px;
        border: none;
        outline: none;
        cursor: pointer;
        padding: 15px 15px 15px 19px;
        border-radius: 4px;
    }
    .btnComment {
        display: block;
        position: fixed;
        bottom: 20px;
        right: 90px;
        z-index: 99;
        font-size: 18px;
        border: none;
        outline: none;
        cursor: pointer;
        padding: 15px 15px 15px 19px;
        border-radius: 4px;
    }

    .btnTop:hover {
        background-color: #555;
    }

    .sticky {
        width: 100%;
        height: 50px;
        z-index: 9999;
        position: fixed;
        top: 0;
    }
    nav {
		border-bottom: 1px solid rgba(255,62,0,0.1);
		font-weight: 300;
		padding: 0 1em;
		height: 52px;
        color: #ffffff;
        position: -webkit-sticky; /* Safari */
        position: sticky;
        top: 0;
    }
    .focus {
		color: #02b875;
	}
	/* clearfix */
	a {
		color: #ffffff;
		text-decoration: none;
	}
	.project-name {
		width: calc(100% - 100px);
		max-width: 800px;
		padding-left: 0em!important;
		padding-right: 0em!important;
		margin-left: 0!important;
		margin-right: 0!important;
		overflow: hidden;
		height: 2em;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}
	.list-inline-logo {
        font-size: 1.5em;
        padding: 10px 20px;
		margin-right: 10px;
		cursor: pointer;
	}
	.list-inline-logo a {
		text-decoration: none;
	}
	.text-orange {
		color: #FFA500;
		text-decoration: none;
    }

    .list-inline-right-menu {
        font-size: 1.5em;
        padding: 0.2em 0em;
		margin-right: 10px;
        cursor: pointer;
        position: absolute;
        top: 0;
        right: 0;
	}
    .btn-round {
        border-radius: 50%;
        height: 2.2em;
        width: 2.2em;
        border: 0px;
        color: #fff;
        font-size: 0.8em;
    }

    /* CONTENT */
    
    .item-content {
        color: #fff; 
        display: block; 
        margin-top: 1em;
        font-size: 1.4em;
    }
    :global(.force-2p:nth-child(even) article) {
        text-align: left;
    }
    .force-2p {
        vertical-align: top;
        width: 50%;
    }
    .force-1p {
        width: 100%;
    }
    .width100 {
        width: 100%!important;
    }
    .chapter-list {
        height: 2em;
        font-size: 0.9em;
        padding: 0 0.5em;
        width: 100%;
        color: #000;
    }

    .modal-load {
        display: block;
        position: absolute;
        top: 0em;
        margin: auto;
        width: 100%;
        margin-top: 6em;
        color: #fff;
    }

    .t-center {
        text-align: center;
    }
    .t-right {
        text-align: right;
    }

    .btn-option {
        display: inline-block;
        width: 2em;
        border-radius: 0.2em;
        border: 0;
    }

    .licenseProject {
		color: #fff;
		text-align: center;
		font-size: 2em;
        margin-top: 4em;
	}
</style>

<script>
    import { pageName } from '../stores.js'
    import { base_url, base_url_fs, base_url_api } from '../configure.js';
    import { onMount } from 'svelte';
    import ListItem from '../components/ListItem.svelte';
    import EditorInfo from '../comp/reader/editorInfo.svelte';
    import ChapterComment from '../comp/reader/chapterComment.svelte';
    import Footer from '../comp/reader/footer.svelte';
    import { getDatestampForJson, getProjectSpecByCode, convertReaderURLComment } from '../components/uiUtility.svelte';
    import Modal from '../components/Modal.svelte';

    export let pid = null;
    export let cno = null;
    export let projectType = null;

    //Option
    let opt2Page = false;
    let optFitHeight = false;

    //Parameter
    let windowHeight = 100;
    let windowWidth;
    $: calPageHeight = windowHeight - 95;
    let openTopmenu = true;
    let selectedChapter = 0;
    let gblShowModalLoading = true;
    let isMaxChapter = false;
    let isFirstChapter = false;

    //Modal

    //Data

    let gblProjInfo = {
        np_project_id: '',
        np_name: 'Loading...',
        np_info: 'Loading...',
        author_name: 'Loading...',
        artist_name: 'Loading...',
        np_type: '',
        np_web: 'Loading...',
        np_licensed_by: 'Loading...',
        np_created_date: 'Loading...',
        np_updated_date: 'Loading...',
        np_status: 'Loading...',
        np_view: 'Loading...'
    };
    let gblChapterList = [];
    let gblChapterInfo = {
        cu_displayname: '',
        nc_chapter_id: '',
        nc_chapter_name: '',
        nc_chapter_no: '',
        nc_created_date: '',
        nc_provider: ''
    }
    let projectSpec = { 
		fullNameCap: '', 
		fullName: '', 
		dir: '' 
	};

    let cid = 1;
    let listItem = [];
    let readStyle = "jp";
    let providerId = null;
    let gblProviderInfo = {
        providerName: null,
        providerType: null,
        slogan: null,
        homepage: null,
        facebook: null,
        website: null
    };
    let modalOpen = false;
    let modalOpenPTow = false;
    let gblFlgLicense = false;
    let licenseBy = "";

    onMount(async () => {
        //Load Parameter & Option
        //TODO - Later
        // loadParameter();
        
        //Load Spec
        projectSpec = getProjectSpecByCode(projectType);

        //Load Project + Chapter List Info, load chapter info
        selectedChapter = cno;
        let tmp = await loadProjectDetail();
        
        //Update title of page
        updateTitle();

        //Render FB Comment
        if(FB && FB.XFBML.parse()) {
            FB.XFBML.parse();
        }

        updateProjectView();

        addEventPTow();

        setInterval(AccessClipboardData(), 300);
    });

    function addEventPTow() {
        document.addEventListener("keyup", function (e) {
            var keyCode = e.keyCode ? e.keyCode : e.which;
            if(keyCode == 44) {
                modalOpenPTow = true;
                stopPrntScr();
                
            }
        });
    }
    function stopPrntScr() {
        var inpFld = document.createElement("input");
        inpFld.setAttribute("value", ".");
        inpFld.setAttribute("width", "0");
        inpFld.style.height = "0px";
        inpFld.style.width = "0px";
        inpFld.style.border = "0px";
        document.body.appendChild(inpFld);
        inpFld.select();
        document.execCommand("copy");
        inpFld.remove(inpFld);
    }
    function AccessClipboardData() {
        try {
            window.clipboardData.setData('text', "Access Restricted");
        } catch (err) {
        }
    }
    

    async function loadParameter() {
        //Option 2 Page
        let tmpOpt2Page = localStorage.getItem("nekopost_comic_opt2page");
        if(tmpOpt2Page) {
            opt2Page = await localStorage.getItem("nekopost_comic_opt2page");
            // console.log("GET TRUE", tmpOpt2Page);
        } else {
            // console.log("GET FALSE");
        }

        //Option Fit Height
        let tmpFitHeight = localStorage.getItem("nekopost_comic_optfitheight");
        if(tmpFitHeight === "true") {
            optFitHeight = localStorage.getItem("nekopost_comic_optfitheight");
        }
    }

    async function loadProjectDetail() {
        let res_data = [];
        const res = await fetch(base_url_api + 'getProjectDetailFull/' + pid + getDatestampForJson(15));
        if(res.ok) {
            res_data = await res.json();

            if(res_data.code === 'RES-20001') {
                gblProjInfo = res_data.projectInfo;
                if(typeof gblProjInfo === undefined || gblProjInfo === null) {
                    window.location.href = base_url + "404";
                } else if (gblProjInfo.np_status == 0 || gblProjInfo.np_status == 4) {
                    window.location.href = base_url + "404";
                } else if(gblProjInfo.np_status == 3) {
                    gblFlgLicense = true;
                    licenseBy = gblProjInfo.np_licenced_by;
                    return false;
                }

                // gblProjectCategoryUsed = res_data.projectCategoryUsed;
                gblChapterList = res_data.projectChapterList;
                gblChapterInfo = gblChapterList.filter(i => cno == i.nc_chapter_no);
                if(typeof gblChapterInfo !== undefined) {
                    gblChapterInfo = gblChapterInfo[0];
                    
                    loadChapterInfo();
                }

                if(cno === gblChapterList[0].nc_chapter_no) {
                    isMaxChapter = true;
                } else {
                    isMaxChapter = false;
                }

                if(cno == gblChapterList[gblChapterList.length - 1].nc_chapter_no) {
                    isFirstChapter = true;
                } else {
                    isFirstChapter = false;
                }
            }
        } else {
            console.log("ERROR: loadProjectDetail => " + pid);
        }
    }

    async function loadChapterInfo() {
        //Scroll to Top
        window.scrollTo(0, 0);
        cid = gblChapterInfo.nc_chapter_id;

        if(typeof cid !== undefined) {
            let tmpNull = [];
            listItem = tmpNull;

            let tmpDataURL = base_url_fs + projectSpec.dir + '/' + pid + "/"+ cid + "/" + pid +"_" + cid + ".json" + getDatestampForJson(5);
            const res = await fetch(tmpDataURL);
            
            if(res.ok) {
                let res_data = await res.json();

                let tmpListItem = convertFileNameToFullUrl(res_data.pageItem);
                listItem = tmpListItem.sort(compareArray);
                providerId = res_data.nc_owner_id;
                //loadProviderInfo(providerId);

                readStyle = res_data.readStyle;
            } else {
                console.log("ERROR: LoadChapterInfo => " + pid);
            }
            
            gblShowModalLoading = false;
        }
    }

    function updateTitle() {
        pageName.update(()=> 'Ch.' + cno + ' - ' + gblProjInfo.np_name);
    }

    async function loadProviderInfo(pid) {
        let tmpDataURL = base_url + "/data_json/group_" + pid + ".json" + getDatestampForJson(5);

		fetch(tmpDataURL).then(res=>{
            return res.json();
        }).then(data => {
            if(data.responseCode === "RES-20001") {
                
                gblProviderInfo = data.providerInfo;

            } else {
                console.log("ERROR: " + data.responseCode);
            }
        }).catch(err=>{
            console.log("ERROR: loadProviderInfo (" + pid + ")");
        });
	}

    
    function convertFileNameToFullUrl(listPageItem) {
        let result = [];

        for(let i=0; i<listPageItem.length; i++) {
            let tmpfileName = base_url_fs + 'collectManga/' + pid + "/"+ cid + '/' + listPageItem[i].fileName;
            listPageItem[i].fileName = tmpfileName;
            result.push(listPageItem[i]);
        }
        
        return result;
    }
    
    function getChapterIdFromList(chapterList, cno) {
        let result = 0;
        for(let i=0; i < chapterList.length; i++) {
            if(chapterList[i].nc_chapter_no === cno) {
                result = chapterList[i].nc_chapter_id;
                break;
            }
        }
        return result;
    }

    function compareArray(a, b) {
        if ( a.pageNo * 1 < b.pageNo * 1 ){
            return -1;
        } else {
            return 1;
        }
    }
    
    function handleOpt2Page() {
        opt2Page = !opt2Page;
        // localStorage.setItem("nekopost_comic_opt2page",opt2Page);
    }

    function handleOptFitHeight() {
        optFitHeight = !optFitHeight;
        // localStorage.setItem("nekopost_comic_optfitheight",optFitHeight);
    }

    function handleClickAnywhere() {
        openTopmenu = !openTopmenu;
        // console.log("handleClickAnywhere!");
    }
    function handleChangeChapter() {
        // loadChapterInfo(selectedChapter); //cid
        let newURL = window.location.href.replace(/\/[^\/]*$/, '/' + selectedChapter);
        window.location.href = newURL;
    }
    function handleChangeChapterByBtn(direction) {
        //Prevent over select chapter
        if((isMaxChapter && direction === 'next') 
            || (isFirstChapter && direction === 'previous')) {
            return false;
        }

        let tmpChapterList = [];
        for(let i=0;i<gblChapterList.length;i++) {
            tmpChapterList.push(gblChapterList[i].nc_chapter_no);
        }

        let tmpIndex = tmpChapterList.indexOf(selectedChapter);

        if(direction === "previous") {
            tmpIndex++;
        } else {
            tmpIndex--;
        }

        let newURL = window.location.href.replace(/\/[^\/]*$/, '/' + tmpChapterList[tmpIndex]);
        window.location.href = newURL;
    }

    async function updateProjectView() {
        if(getCookie("nekopost_" + pid) != 1000) {
            setCookie("nekopost_" + pid, 1000, 1);
            const response = fetch(base_url_api_project + 'ajaxProjectView/' + pid);
            console.log("Cookie!");
        } else {
            console.log("No Cookie");
        }
    }
    function getCookie(key) {
        var keyValue = document.cookie.match('(^|;) ?' + key + '=([^;]*)(;|$)');
        return keyValue ? keyValue[2] : null;
    }
    function setCookie(key, value, expiry) {
        var expires = new Date();
        expires.setTime(expires.getTime() + (expiry * 60 * 60 * 1000)); //Expiry = number of hours
        document.cookie = key + '=' + value + ';expires=' + expires.toUTCString();
    }

    let scrollY;
    function handleScrollTop() {
        scrollY = 0;
        // openTopmenu = true;
    }
</script>

<svelte:window 
    bind:innerHeight={windowHeight} 
    bind:innerWidth={windowWidth}
    bind:scrollY={scrollY}
/>

    <div class="print-content">

    <!-- MODALBOX: SELECTOR -->
    <Modal bind:open={modalOpen}>
        <div style="max-width: 640px; margin: auto;">
            <div class="p-4 rounded">
                <table width="100%">
                    <tr>
                        <td colspan="2">
                            <h1 class="pb-4 pt-2 text-center">{gblProjInfo.np_name}</h1>
                            {#await gblChapterList}
                            Loading..
                            {:then}
                                <select class="chapter-list" style="font-size: 1.5em; height: 200px;" bind:value={selectedChapter} on:change="{handleChangeChapter}" size=4>
                                    {#each gblChapterList as item, i}
                                        <option value={item.nc_chapter_no}>Ch.{item.nc_chapter_no} - {item.nc_chapter_name}</option>
                                    {/each}
                                </select>
                            {/await}
                        </td>
                    </tr>
                    <tr>
                        <td width="50%">
                            <div class="btn width100 rounded pl-4 pr-4 pt-8 pb-8 text-center text-white {isFirstChapter === true ? "btn-secondary": "btn-primary"}" on:click={()=>{handleChangeChapterByBtn('previous')}} disabled={isFirstChapter}>
                                <i class="fas fa-arrow-left icon-2x"></i>
                                <br/>
                            </div>
                        </td>
                        <td width="50%">
                            <div class="btn rounded width100 pl-4 pr-4 pt-8 pb-8 text-center text-white {isMaxChapter === true ? "btn-secondary": "btn-primary"}" on:click={()=>{handleChangeChapterByBtn('next')}} disabled={isMaxChapter}>
                                <i class="fas fa-arrow-right icon-2x"></i>
                                <br/>
                            </div>
                        </td>
                    </tr>
                </table>
            </div>
        </div>
    </Modal>

    <!-- {#if scrollY > 50}
    <button class="btnTop btn btn-primary" on:click={handleScrollTop}>TOP</button>
    {/if} -->
    {#if windowWidth < 1000}
        <button class="btn btn-primary" style="right: 10px; position: fixed; bottom: 20px; padding-left: 16px; z-index: 9999;" on:click={handleScrollTop}><i class="fal fa-arrow-to-top"></i></button>
        <a href="{convertReaderURLComment(window.location.href)}#commentBox" target="_parent">
            <button class="btn btn-primary" style="right: 65px; position: fixed; bottom: 20px; z-index: 9999;"><i class="fad fa-comments-alt"></i></button>
        </a>
    {:else}
        <button class="btnTop btn btn-primary" on:click={handleScrollTop}><i class="fal fa-arrow-to-top"></i></button>
        <a href="{convertReaderURLComment(window.location.href)}#commentBox" target="_parent">
            <button class="btnComment btn btn-primary"><i class="fad fa-comments-alt"></i></button>
        </a>
    {/if}

    <!-- To be inject HEADER, CHAPTER LIST -->
    <nav class="bg-c2 sticky bg-darker" style="visibility: {openTopmenu == true ? "visible" : "hidden"}">
        {#if typeof gblProjInfo !== undefined}
            {#if windowWidth < 1000}
            <div class="float-right t-right" style="padding-top: 0.8em;">
                <button class="btn btn-primary text-white" on:click={event => modalOpen = true}><i class="fas fa-list-ul"></i> Chapter</button>
            </div>
            <div class="float-left list-inline-logo project-name">
                <a href='{base_url}' target="_parent">N<span class="text-orange">P</span></a> - <a href="{base_url}{projectSpec.fullName}/{gblProjInfo.np_project_id}" target="_parent">{gblProjInfo.np_name}</a>
            </div>
            {:else}
            <div class="float-left list-inline-logo project-name">
                <a href='{base_url}' target="_parent">NEKOPOST.<span class="focus">NET</span></a> - <a href="{base_url}{projectSpec.fullName}/{gblProjInfo.np_project_id}" target="_parent">{gblProjInfo.np_name}</a>
            </div>

            <div class="mt-1 float-right list-inline-right-menu">
                <table>
                    <tr>
                        <!-- LG-MENU for 2 Page and Fit Height -->
                        <td class="pr-2">
                            <!-- <button class="btn-option {opt2Page == true ? " active":""}" on:click={handleOpt2Page} alt="Show 2 page">
                                <i class="fad fa-columns"></i>
                            </button> -->
                            <button class="btn-option {optFitHeight == true ? " active":""}" on:click={handleOptFitHeight} alt="Fit height">
                                <i class="fad fa-arrows-alt-v"></i>
                            </button>
                        </td>
                        <td><button class="btn-round {isFirstChapter === true ? "bg-secondary": "bg-primary"}" on:click={()=>{handleChangeChapterByBtn('previous')}} disabled={isFirstChapter}><i class="fas fa-arrow-left"></i></button></td>
                        <td>
                            {#await gblChapterList}
                                Loading..
                            {:then}
                                <select class="chapter-list" bind:value={selectedChapter} on:change="{handleChangeChapter}">
                                    {#each gblChapterList as item, i}
                                        <option value={item.nc_chapter_no}>Ch.{item.nc_chapter_no} - {item.nc_chapter_name}</option>
                                    {/each}
                                </select>
                            {/await}
                        </td>
                        <td><button class="btn-round {isMaxChapter === true ? "bg-secondary": "bg-primary"}" on:click={()=>{handleChangeChapterByBtn('next')}} disabled={isMaxChapter}><i class="fas fa-arrow-right"></i></button></td>
                    </tr>
                </table>
            </div>

            {/if}
        {/if}
    </nav>
    <div style="height: 1px;"></div>

    <!-- LOADING -->
    {#if gblShowModalLoading && gblFlgLicense == false}
    <div class="t-center modal-load">
        <img src="{base_url}demo/neko-loading.gif" alt="">
        <br><span>Rendering awesome content!</span>
    </div>
    <div style="clear: both; height: 600px;"></div>
    {/if}
    {#if gblFlgLicense == true}
        <div class="licenseProject">
            This project has been licensed by {licenseBy}, Please supports their product :)
        </div>
    {/if}

    <!-- LIST ITEM -->
    <div class="mt-10" width="100%" on:click={handleClickAnywhere}>
        <div style="height: 1px;"></div>

        {#each listItem as item, i}
            <div class="t-center item-content
                {opt2Page == true ? " force-2p " : " force-1p "}
                {item.width > item.height ? " width100": ""}
                {readStyle == "jp" ? "float-right" : "float-left"}
                ">
                {#if opt2Page == true && item.width < item.height && readStyle != "jp" }
                    <div class="t-{item.pageNo%2 === 1 ? "right" : "left"}">
                    <ListItem 
                        item={item.fileName} lazy={i > 1} 
                        styleX={optFitHeight == true ? ' height: ' + calPageHeight + 'px!important; width: auto!important;':' max-width: 100%!important; height: auto;'} 
                        pageSide={item.pageNo%2 === 1 ? "right" : "left"}
                    />
                    </div>
                {:else if opt2Page == true && item.width < item.height && readStyle == "jp" }
                    <div class="t-{item.pageNo%2 === 1 ? "left" : "right"}">
                        <ListItem item={item.fileName} lazy={i > 1} styleX={optFitHeight == true ? ' height: ' + calPageHeight + 'px!important; width: auto!important;':' max-width: 100%!important; height: auto;'} />
                    </div>
                {:else}
                    <div>
                        <ListItem item={item.fileName} lazy={i > 1} styleX={optFitHeight == true ? ' height: ' + calPageHeight + 'px!important; width: auto!important;':' max-width: 100%!important;'} />
                    </div>
                {/if}
                <span>Page No.{item.pageNo}/{listItem.length}</span>
            </div>
            {#if item.width > item.height}
            <br style="clear: both;"/>
            {/if}
        {/each}


        <br style="clear: both"/>

        <!-- CHAPTER -->
        {#if gblShowModalLoading === false}
        <div style="max-width: 640px; margin: auto; margin-top: 4em;">
            <div class="p-4 bg-darker rounded">
                <table width="100%">
                    <tr>
                        <td width="100px">
                            <button class="btn text-white {isFirstChapter === true ? "bg-secondary": "btn-primary"}" on:click={()=>{handleChangeChapterByBtn('previous')}} disabled={isFirstChapter}><i class="fas fa-arrow-left"></i><span class="hide-sm">&nbsp;PREVIOUS</span></button>
                        </td>
                        <td>
                            {#await gblChapterList}
                            Loading..
                            {:then}
                                <select class="chapter-list" style="font-size: 1.5em;" bind:value={selectedChapter} on:change="{handleChangeChapter}">
                                    {#each gblChapterList as item, i}
                                        <option value={item.nc_chapter_no}>Ch.{item.nc_chapter_no} - {item.nc_chapter_name}</option>
                                    {/each}
                                </select>
                            {/await}
                        </td>
                        <td width="100px">
                            <span style="text-align: left;">
                                <button class="btn text-white {isMaxChapter === true ? "bg-secondary": "bg-primary"}" on:click={()=>{handleChangeChapterByBtn('next')}} disabled={isMaxChapter}><i class="fas fa-arrow-right"></i><span class="hide-sm">&nbsp;NEXT</span></button>
                            </span>
                        </td>
                    </tr>
                </table>
            </div>
        </div>
        {/if}
        <br/><br/>

        {#if providerId !== null}
        <br style="clear: both"/>
        <!-- <EditorInfo 
            base_url={base_url}
            gblProviderInfo={gblProviderInfo} 
        /> -->
        {/if}

        <!-- CHAPTER COMMENT -->
        <br style="clear: both" id="commentBox" />
        <!-- <ChapterComment
            projectType=''
            projectId=''
            cno=''
        /> -->
        <div class="d-flex flex-column-fluid">
            <div class="container">
                <div class="row">
                    <div class="col-12 col-lg-3 mb-2">
                        <div class="card card-custom custom-sticky">
                            <div class="mt-4 ml-5 mb-0 pb-0">
