<style>
	a {
		color: #fff;
	}
	.box {
		display: block;
		background-color: #292B2F;
		border-radius: 0.4em;
		position: relative;
		overflow: hidden;
		transition: box-shadow .3s;
		top: 0;
	}
	.box:hover {
		background-color: #202225;
		cursor: pointer;
		transition: 0.3s;
		box-shadow: 0 0 11px rgba(0, 0, 0, 0.3); 
	}
	.project-title {
		font-weight: 500;
		font-size: 1.3em;
		display: block;
		line-height: 110%;
		padding-top: 0.2em;
		padding-bottom: 0.3em;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	span {
		display: block;
		text-overflow: ellipsis;
		white-space: nowrap;
		overflow: hidden;
		max-height: 1.4em;
	}
	.img-preview {
		width: 72px;
		height: 72px;
		border-radius: 0.4em;
		float: left;
		margin-right: 1em;
	}
	.provider {
		position: absolute;
		left: 92px;
		bottom: 0.8em;
		width: calc(100% - 95px);
		display: block;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	span {
		color: #989A9D;
	}

	.more {
		text-align: center;
		padding: 1em 0;
		height: 48px;
		color: #fff;
		border-bottom: 3px solid rgb(82, 82, 82);
	}
	.more:hover {
		background-color: #202225;
		cursor: pointer;
	}
</style>

<script>
	//DEPENDENCY
	import { base_url, base_url_fs, base_url_api } from '../configure.js';
	import { onMount } from 'svelte';
	import { getDatestampForJson, createTmpArray, getProjectSpecByCode, getTitleFromCode, showConvertDate } from '../components/uiUtility.svelte';
	import { SkeletonText, SkeletonImage } from 'skeleton-elements/svelte';
	import ImageItemP from '../components/ImageItemPreview.svelte';

	export let projType = ''; //Default as 'm'
	export let listSize = 0;
	export let listType = ''; //l=Latest chapter, r=Random, c=Completed,n=New Arrival
	export let splitCol = 0;
	export let isEnableMore = false;
	let isEndLoad = false;

	let apiName = "getLatestChapterOffset";
	$: switch(listType){
		case "l":
			apiName = "getLatestChapterOffset";
		break;

		case "r":
			apiName = "getRandomProject";
		break;

		case "n":
			apiName = "getNewArrivalProject";
		break;
	};
	

	//Data
	let tmpDataAll = [];
	let tmpDataCol = [];
	let colSize = 0;
	let tmpClass = "";
	let offset = 0;

	let projectSpec = { 
		fullNameCap: '', 
		fullName: '', 
		dir: '' 
	};
	let windowWidth;

	let tmpLoadingArray = [];
	let flagLoading = true;

	//Render loading
	let tmpLoadAll = [];
	let tmpLoadCol = [];

	onMount(async () => {
		flagLoading = true;
		computeCSSClass(splitCol);
		projectSpec = getProjectSpecByCode(projType);

		//1.Render loading
		tmpLoadCol = createTmpArray(listSize);

		//2.Render data from API
		tmpDataAll = await loadLatestUpdateChapter(projType); //Load data from API
		handleMore();

		// console.log(projType, tmpDataCol);
	});

	//Data
	async function loadLatestUpdateChapter() {
		let res_data = [];
		let tmpResponseData = [];
		const res = await fetch(base_url_api + apiName + '/' + projType + '/' + offset + getDatestampForJson(5));
        if(res.ok) {
			res_data = await res.json();
			
			if(res_data.code === "RES-20001") {
				tmpResponseData = res_data.listItem;
			} else {
				//TODO - ERROR HAPPENDED
			}
        } else {
            console.log("ERROR: loadLatestUpdateChapter");
			throw new Error(res.json());
        }

		flagLoading = false;
		return tmpResponseData;
	}

	//Utility
	function computeCSSClass(splitCol) {
		tmpClass = "col-12";
		switch(splitCol) {
			case '3':
				tmpClass = "col-12 col-sm-6 col-md-6 col-lg-4";
				break;
			case '2':
				tmpClass = "col-12 col-lg-6";
				break;
		}
	}

	function computeEachCol(tmpCol) {
		let tmpArr = [];

		tmpCol.forEach(function(item, index){
			let tmpSlice = tmpDataAll.splice(0, colSize);
			tmpArr.push(tmpSlice);
		});

		return tmpArr;
	}

	async function handleMore() {
		//Step 1 : If current item remains left than listSize, do load ajax
		if(tmpDataAll.length < listSize) {
			flagLoading = true;
			offset = offset + 1;
			let tmpOffsetItem = await loadLatestUpdateChapter();
			tmpDataAll = tmpDataAll.concat(tmpOffsetItem);
		}

		//Step 2 : Assign 
		let tmpAddArray = tmpDataCol.concat(tmpDataAll.splice(0, listSize));
		tmpDataCol = tmpAddArray;
		
		if(offset === 7) {
			isEndLoad = true;
		}
	}

</script>

<svelte:window 
    bind:innerWidth={windowWidth}
/>


<div class="row p-2">
	{#each tmpDataCol as item}
		<div class="{tmpClass} pl-1 pr-1 {windowWidth < 1000 ? "pb-1" : "pb-4"}">
			<a href="./{projectSpec.fullName}/{item.np_project_id}" alt="">
				<div class="box p-2">
					<!-- <ImageItemP
						item='{base_url_fs}{projectSpec.dir}/{item.np_project_id}/{item.np_project_id}_preview.jpg'
						lazy=true
					/> -->
					<img src="{base_url_fs}{projectSpec.dir}/{item.np_project_id}/{item.np_project_id}_preview.jpg{getDatestampForJson(60)}" class="img-preview" alt="" onerror="this.src='./demo/crissXcross.png';" />
					<div>
						<b class="project-title">{item.np_name}</b>
						{#if listType === "l"}
						<span>Ch.{item.nc_chapter_no} - {item.nc_chapter_name}</span>
						{:else if listType === "n"}
						<span>{showConvertDate(item.np_created_date)}</span>
						{:else if listType === "r"}
						<span>{item.np_no_chapter} Chapter{item.np_no_chapter >= 3 ? "s" : ""}</span>
						{/if}
					</div>
					<div class="pl-2 pr-2 provider">
						<div class="float-left">
							{#if listType === "r"}
								<i class="fad fa-user"></i> <b>Author</b> ( {item.npo_name.trim()} )
							{:else if listType === "n"}
								<i class="fad fa-user"></i> <b>Author</b> ( {item.npo_name.trim()} )
							{:else}
								<i class="fad fa-palette"></i> {item.nc_provider.trim()}
							{/if}
						</div>
						{#if listType === "l"}
						<div class="float-right d-none d-xl-block">
							{showConvertDate(item.nc_created_date)}
						</div>
						{/if}
					</div>
				</div>
			</a>
		</div>
	{/each}

	{#if flagLoading === true}
		{#each tmpLoadCol as item}
			<div class="{tmpClass} pl-1 pr-1 pb-4">
				<div class="box p-2">
					<div class="img-preview">
						<SkeletonImage effect='wave' width='72' height='72' />
					</div>
					<div>
						<b class="project-title"><SkeletonText effect='wave'>....................................</SkeletonText></b>
						<span><SkeletonText effect='wave'>..................</SkeletonText></span>
					</div>
					<div class="pr-2 provider">
						<div class="float-left">
							<SkeletonText effect='wave'>..................</SkeletonText>
						</div>
						<div class="float-right">
							<SkeletonText effect='wave'>..................</SkeletonText>
						</div>
					</div>
				</div>
			</div>
		{/each}
	{/if}
</div>
{#if isEnableMore === 'true'}
	{#if isEndLoad === true}
		<div class="more">Done load content ;w;b</div>
	{:else}
		<div class="more" on:click={handleMore}><i class="fa fa-chevron-down"></i></div>
	{/if}
{/if}