<script>
    import { onMount, afterUpdate } from 'svelte';
    import { base_url, base_url_fs, base_url_api } from '../configure.js';
    import { SkeletonText, SkeletonImage } from 'skeleton-elements/svelte';
    import { getDatestampForJson } from '../components/uiUtility.svelte';
    // import ImageItem from '../components/ImageItem.svelte';

    let listProjectAlphabet = [];
    let listProjectCurrent = [];
    let listProjectCurrentShow = [];
    let listProjectAlphabetCurrent = [];

    //Param
    let moreSize = 40;
    let isEndLoad = false;

    onMount(async () => {
        let tmpX = loadProjectDIR();
    });

    afterUpdate(() => {
        console.log("after update : Side Menu");
		//(adsbygoogle = window.adsbygoogle || []).push({});
        try {
            window.adsbygoogle = window.adsbygoogle || []
    	    window.adsbygoogle.push({})

            setTimeout(function () {
                window.adsbygoogle = window.adsbygoogle || []
                window.adsbygoogle.push({})
            }, 3500);
        } catch (err) {
            console.log(err.message);
        }
	});

    function handleMore() {
		let tmpAddArray = listProjectCurrentShow.concat(listProjectCurrent.splice(0, moreSize));
		listProjectCurrentShow = tmpAddArray;

		if(listProjectCurrent.length*1 === 0) {
			isEndLoad = true;
		}

        // console.log("2", listProjectAlphabet['a'].length);
	}

    function changeAlphabet(alphabet) {
        isEndLoad = false;
        if(listProjectAlphabet[alphabet] !== undefined) {
            // console.log("1", listProjectAlphabet[alphabet].length);
            listProjectCurrent = new Array();
            listProjectCurrent = JSON.parse(JSON.stringify(listProjectAlphabet[alphabet]));
            // console.log("listProjectCurrent", listProjectCurrent.length);
            listProjectCurrentShow = [];

            handleMore();
        }        
    }

    async function loadProjectDIR() {
        let res_data = [];
        const res = await fetch(base_url_api + 'getDirAll' + getDatestampForJson(5));
        if(res.ok) {
            res_data = await res.json();

            if(res_data.code === 'RES-20001') {
                let listProject = res_data.listItem;
                let tmpArrOther = [];
                let tmpArr = [];
                let tmpData = await listProject.reduce((r, e) => {
                    let alphabet = e.np_name[0].toLowerCase();
                    if(alphabet.match(/[a-z]/i)){
                        if(tmpArr[alphabet] !== undefined) {
                            tmpArr[alphabet].push(e);
                        } else {
                            tmpArr[alphabet] = [e];
                        }
                        // if(!tmpArr[alphabet] > 0) {
                        //     tmpArr[alphabet].push(e);
                        // } else {
                        //     tmpArr[alphabet] = [];
                        //     tmpArr[alphabet].push(e);
                        // }

                    } else {
                        tmpArrOther.push(e);
                    }
                });

                // console.log(tmpArr);
                // console.log(tmpArrOther);
                tmpArr['#'] = tmpArrOther;
                listProjectAlphabet = tmpArr;
                listProjectCurrent = listProjectAlphabet['#'];

                handleMore();
            }

        } else {
            console.log("ERROR: loadProjectDetail => " + pid);
        }
    }

    function convertProjectStatusFromDBCode(statusCode) {
        let result = "";
        switch(statusCode) {
            case "1": result = "On Going";
            break;

            case "2": result = "Completed";
            break;

            case "3": result = "Licensed";
            break;
        }
        return result;
    }

    function convertProjectIconFromType(projectType) {
        let result = "";
        switch(projectType) {
            case "m": result = "bg-danger-o-80";
            break;

            case "d": result = "bg-warning-o-80";
            break;

            case "n": result = "bg-info-o-80";
            break;

            case "f": result = "bg-success-o-80";
            break;
        }
        return result;
    }

    function convertProjectType(type) {
        let result = '';
        switch(type) {
            case "m": result = "Manga";
            break;
            case "d": result = "Original Comic";
            break;
            case "n": result = "Novel";
            break;
            case "f": result = "Original Novel";
            break;
        }

        return result;
    }

    function convertProjectTypeFull(projectType) {
        let result = "";
        switch(projectType) {
            case "m": result = "manga";
            break;
            case "d": result = "comic";
            break;
            case "n": result = "novel";
            break;
            case "f": result = "fiction";
            break;
        }

        return result;
    }
</script>

