	import { getDatestampForJson, showDatePattern, convertProjectStatusFromDBCode, getProjectSpecByCode, numberWithCommas, validURL, scrollToTopByDiv } from '../components/uiUtility.svelte';
    
    import ProjectDetailHeader from '../comp/project/projectDetailHeader.svelte';
    import ChapterList from '../comp/project/chapterList.svelte';
    import Contributor from '../comp/project/contributor.svelte';
    import RelatedItem from '../comp/project/relatedItem.svelte';
    
    export let projectType = '';
    export let pid = '';

    //Variable
    let gblProjInfo = {
        np_project_id: '',
        np_name: 'Loading...',
        np_info: 'Loading...',
        author_name: 'Loading...',
        artist_name: 'Loading...',
        np_type: '',
        np_web: 'Loading...',
        np_licensed_by: 'Loading...',
        np_created_date: 'Loading...',
        np_updated_date: 'Loading...',
        np_status: 'Loading...',
        np_view: 'Loading...'
    };
    let gblProjectCategoryUsed = [];
    let gblChapterList = [];
    let gblProviderList = [];
    let projectSpec = { 
		fullNameCap: '', 
		fullName: '', 
		dir: '' 
	};
    let gblFlgLicensed = false;
    let gblLicenseBy = "";

    onMount(async () => {
        //Load data
        let tmp = await loadProjectDetail();

        //Load Spec
        projectSpec = getProjectSpecByCode(projectType);

        //Render FB Comment
        if(FB && FB.XFBML.parse()) {
            FB.XFBML.parse();
        }
        // FB.XFBML.parse();

        //Render data
        updateTitle(); 

        // updateProjectView();

        //Scroll to top when load
        scrollToTopByDiv("projectDetailTop");

    });

    afterUpdate(() => {
        //Update view
        updateProjectView();

        console.log("after update : Side Menu");
		//(adsbygoogle = window.adsbygoogle || []).push({});
        try {
            window.adsbygoogle = window.adsbygoogle || []
    	    window.adsbygoogle.push({})
        } catch (err) {
            console.log(err.message);
        }
	});

    function updateTitle() {
        pageName.update(()=> gblProjInfo.np_name);
    }

    async function updateProjectView() {
        if(getCookie("nekopost_" + pid) != 1000) {
            setCookie("nekopost_" + pid, 1000, 1);
            const response = fetch(base_url_api_project + 'ajaxProjectView/' + pid);
            console.log("Cookie!");
        } else {
            console.log("No Cookie");
        }
    }
    function getCookie(key) {
        var keyValue = document.cookie.match('(^|;) ?' + key + '=([^;]*)(;|$)');
        return keyValue ? keyValue[2] : null;
    }
    function setCookie(key, value, expiry) {
        var expires = new Date();
        expires.setTime(expires.getTime() + (expiry * 60 * 60 * 1000)); //Expiry = number of hours
        document.cookie = key + '=' + value + ';expires=' + expires.toUTCString();
    }

    async function loadProjectDetail() {
        let res_data = [];
        const res = await fetch(base_url_api + 'getProjectDetailFull/' + pid + getDatestampForJson(5));
        if(res.ok) {
            res_data = await res.json();

            if(res_data.code === 'RES-20001') {
                gblProjInfo = res_data.projectInfo;

                if(gblProjInfo === null) {
                    window.location.href = base_url + "404";
                }

                if(res_data.projectCategoryUsed != null) {
                    gblProjectCategoryUsed = res_data.projectCategoryUsed;
                }

                if(gblProjInfo.np_status == 1 || gblProjInfo.np_status == 2) {
                    gblChapterList = res_data.projectChapterList;
                } else if (gblProjInfo.np_status == 3){
                    gblFlgLicensed = true;
                    gblLicenseBy = gblProjInfo.np_licenced_by;
                    console.log("gblLicenseBy",gblLicenseBy);
                } else {
                    window.location.href = base_url + "404";
                }
                
                //Distinct for provider list
                gblProviderList = distinctChapterProvider(gblChapterList);
            }

        } else {
            console.log("ERROR: loadProjectDetail => " + pid);
        }
    }

    function distinctChapterProvider(listChapter) {
        let resultProvider = [];
        
        if(listChapter !== null) {
            for(let i = 0; i < listChapter.length; i++) {
                if(!resultProvider.includes(listChapter[i].nc_owner_id)) {
                    resultProvider.push(listChapter[i].nc_owner_id);
                }
            }
        }
        
        return resultProvider;
    }

</script>

