
<script>
    export let base_url = '';
    export let showInfo = true;
    export let showVertical = false;
    export let gblProviderInfo = {
        providerName: '',
        providerType: '',
        slogan: '',
        homepage: '',
        facebook: '',
        website: ''
    }

    export let showLink = true;
</script>

<div class="card card-custom card-stretch bg-darker">
    <!--begin::Body-->
    <div class="card-body {showVertical === true ? "pl-4 pr-4 pb-2 pt-2" : "pb-4 pt-8"}">
        <!--begin::User-->
        <div class="{showVertical === true ? "" : "text-center mb-6"}">
            <div class="symbol symbol-60 symbol-circle symbol-xl-90{showVertical === true ? " float-left mr-4" : ""}">
                <div class="symbol-label" style="background-image:url('{base_url}/demo/no_image_preview.jpg')"></div>
            </div>

            <!-- SHOW MOBILE HOME BUTTON -->
            <div class="d-block d-md-none d-lg-none d-xl-none float-right pl-1">
                <a href="/" class="btn btn-icon btn-danger font-weight-bold py-3 mb-1 text-center btn-block btn-homepage"><i class="fad fa-home-lg"></i></a>
            </div>

            <!-- GROUP INFO -->
            <h4 class="font-weight-bold my-2">{gblProviderInfo.providerName}fasd asdfasd fasd</h4>
            <div class="text-muted mb-2">{gblProviderInfo.providerType}</div>
        </div>
        {#if showInfo === true}
        <p class="pb-6 pt-6 text-center" style="clear:both;">
            {gblProviderInfo.slogan}
        </p>
        {/if}
        {#if showLink}
        <div class="mb-12 text-center">
            <a href="/" class="btn btn-icon btn-circle btn-light-facebook mr-2">
                <i class="socicon-facebook"></i>
            </a>
            <a href="/" class="btn btn-icon btn-circle btn-light-twitter mr-2">
                <i class="socicon-twitter"></i>
            </a>
            <a href="/" class="btn btn-icon btn-circle btn-light-google">
                <i class="socicon-google"></i>
            </a>
        </div>
        {/if}

        <div class="d-none d-lg-block">
            <a href="/" class="btn btn-danger font-weight-bold py-3 px-6 mb-1 text-center btn-block">Editor Homepage</a>
        </div>
        
    </div>
    <!--end::Body-->
</div>