import { writable, derived } from 'svelte/store';

export const pageName = writable('Online project platform');

//Stored theme
const storedReaderNovelTheme = localStorage.getItem("readerNovelTheme");
export const readerNovelTheme = writable(storedReaderNovelTheme);
readerNovelTheme.subscribe(value => {
    localStorage.setItem("readerNovelTheme", value === 'light' ? 'light' : 'dark');
});
